import axios from 'axios';
import { Module } from 'vuex';
import { IYearItem } from '@/types/interface';
import { RootState, TestState, IYearOrigin } from './types';

const pastTest: Module<TestState, RootState> = {
  namespaced: true,
  state: {
    yearList: [],
    errorQuz: '',
  },
  mutations: {
    SAVE_YEAR_LIST(state, list) {
      state.yearList = list.map((d: IYearOrigin) => ({
        rid: d.rid,
        title: d.Title,
      }));
    },
    SAVE_ERROR_QUESTION(state, quz) {
      state.errorQuz = quz;
    },
  },
  actions: {
    getYearList({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('Testhistory/GetYear_List')
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_YEAR_LIST', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    saveErrorQuestion({ commit }, quz) {
      commit('SAVE_ERROR_QUESTION', quz);
    },
  },
  getters: {

  },
};

export default pastTest;
