import axios from 'axios';
import { Module } from 'vuex';
import {
  RootState, KindState, ITypeOrigin, IKindOrigin, IPageOrigin, IContentOrigin,
} from './types';

const pastTest: Module<KindState, RootState> = {
  namespaced: true,
  state: {
    typeMenu: [],
    kindMenu: [],
    typePage: [],
    typeContent: [],
  },
  mutations: {
    SAVE_TYPE_MENU(state, types) {
      state.typeMenu = types.map((d: ITypeOrigin) => ({
        typename: d.Typename,
        title: d.Title,
      }));
    },
    SAVE_KIND_MENU(state, types) {
      state.kindMenu = types.map((d: IKindOrigin) => ({
        kindname: d.Kindname,
        title: d.Title,
      }));
    },
    SAVE_TYPE_PAGE_LIST(state, pages) {
      state.typePage = pages.map((d: IPageOrigin) => ({
        pagename: d.Pagename,
        title: d.Title,
      }));
    },
    SAVE_TYPE_PAGE_CONTENT(state, contents) {
      state.typeContent = contents.map((d: IContentOrigin) => ({
        testType: d.TestType,
        title: d.Title,
        note: d.note,
        actId: d.Act_id,
        pageType: d.PageType,
      }));
    },
  },
  actions: {
    getTypeMenu({ commit }) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Menu', {
            publicname: 'jobs',
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_MENU', data.Data);
            resolve();
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getKindMenu({ commit }, type) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Kind_Menu', {
            typename: type,
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_KIND_MENU', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypePageList({ commit }, type) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Page_List', {
            typename: type,
          })
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_PAGE_LIST', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
    getTypePage({ commit }, params) {
      return new Promise((resolve, reject) => {
        axios
          .post('raspapp/TestKind/Type_Page', params)
          .then((response) => {
            const { data } = response;
            if (data.Code === '0000') commit('SAVE_TYPE_PAGE_CONTENT', data.Data);
            resolve(data.Data);
          })
          .catch((error) => {
            reject(error);
          });
      });
    },
  },
  getters: {

  },
};

export default pastTest;
